* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.App {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  background-color: lightgray;
}

.random-quote {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin-top: 50px;
  width: 600px;
  min-height: 300px;
  border-radius: 10px;
  background-color: rgb(91, 164, 212);
}

.random-quote .next-prev-buttons {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.random-quote .next-prev-buttons button {
  width: 150px;
  height: 40px;
  border-radius: 5px;
  color: white;
  background-color: black;
  border: none;
  cursor: pointer;
}

.random-quote p {
  text-align: center;
  padding: 0 20px;
}